.c-quote {
  position:relative;
  color:$white;

  &__inner {
    @include page-color(background-color);
    position: relative;
    padding: 6rem;
    padding-top:calc(7.5rem + 67px);
    padding-left: 0;
    background-image:url('#{$image-path}/icons/quotemark.svg');
    background-position: top 4rem left 0;
    background-size:67px 83px;

    @include breakpoint($break-site-width) {
      background-position: top 4rem left 3rem;
    }

    @include breakpoint($break-site-width) {
      padding-left: 3rem;
    }

    &:before {
      position: absolute;
      top: 0;
      left: -50vw;
      display: block;
      width: 50vw;
      height: 100%;
      content: '';
      @include page-color(background-color);
    }
  }

  &__quote {
    padding-bottom:1.5rem;
  }

  &__person {
    @include font-bold;
    padding-bottom:1rem;
  }

  &__position {
  }

}